body {
  margin: 0;
  font-family: "soohan" -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div {
  font-family: "soohan";
}

html {
  font-size: 62.5%;
}

@font-face {
  font-family: "soohan";
  src: url("./assets/font/SSShinb7.ttf");
}
